<template>
    <div class="iq-top-navbar">
        <nav class="nav navbar navbar-expand-lg navbar-light iq-navbar p-lg-0">
            <div class="container-fluid p-auto">
                <div class="d-flex align-items-center gap-3">
                    <router-link :to="{ name: 'osboha.list' }" class="d-flex align-items-center gap-2 iq-header-logo ">
                        <!-- logo -->
                    </router-link>
                    <h3 class="logo-title d-none d-sm-block" data-setting="app_name"> 180</h3>
                    <a class="sidebar-toggle" @click="toggleSidebar" data-toggle="sidebar" data-active="true">
                        <div class="icon material-symbols-outlined iq-burger-menu">
                            menu
                        </div>
                    </a>
                </div>
                <router-link :to="{ name: 'osboha' }" class="navbar-brand p-0">
                    <img src="@/assets/images/main/osboha-logo.png" alt="logo" class="">
                </router-link>
                <div class="social-media">
                    <p class="mb-0 d-flex">
                        <i  role="button"
                            @click="logout"
                            class="d-flex align-items-center justify-content-center ms-2 me-2 position-relative material-symbols-outlined"
                            style="  -webkit-transform: scaleX(-1); transform: scaleX(-1);">
                            logout
                        </i>
                        <router-link :to="{
                                name: 'user.profile', params: {
                                    user_id: user.id
                                }
                            }" class="d-flex align-items-center justify-content-center ms-2 me-2"> <i
                                class="material-symbols-outlined">person</i>
                        </router-link>
                        <router-link :to="{ name: 'osboha.notification' }"
                            class="d-flex align-items-center justify-content-center ms-2 me-2 position-relative">
                            <i class="material-symbols-outlined">notifications</i>
                            <span v-if="un_read_notifications > 0"
                                class="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-success">
                                {{ un_read_notifications }}
                                <span class="visually-hidden">unread messages</span>
                            </span>
                        </router-link>
                        <router-link :to="{ name: 'osboha.list' }"
                            class="d-flex align-items-center justify-content-center ms-2 me-2">
                            <i class="material-symbols-outlined">home</i>
                        </router-link>

                    </p>
                </div>
            </div>
        </nav>
    </div>
</template>
<script>
import { useStore } from "vuex";
import { computed } from "vue";
import UserInfo from "@/Services/userInfoService";
import Pusher from "pusher-js";
import notificationsServices from "@/API/services/notifications.service";
import helper from "@/utilities/helper";

export default {
    name: "DefaultHeader",
    props: {
        image: {
            type: String,
            default: require("@/assets/images/logo.png"),
        },
    },
    setup() {
        const store = useStore();
        const sidebarType = computed(() => store.getters["setting/sidebar_type"]);
        const toggleSidebar = () => {
            // Code Here
            if (sidebarType.value.includes("sidebar-mini")) {
                store.dispatch(
                    "setting/sidebar_type",
                    sidebarType.value.filter((item) => item !== "sidebar-mini")
                );
            } else {
                store.dispatch("setting/sidebar_type", [
                    ...sidebarType.value,
                    "sidebar-mini",
                ]);
            }
        };
        return {
            store,
            sidebarType,
            toggleSidebar,
        };
    },
    async created() {
        this.notifications = await notificationsServices.listUnreadNotification();
        this.un_read_notifications = Object.keys(this.notifications).length;

    },
    data() {
        return {
            notifications: [],
            un_read_notifications: 0,
        };
    },
    computed: {
        user() {
            return this.$store.getters.getUser;
        },
    },
    mounted() {
        Pusher.logToConsole = true;

        const pusher = new Pusher("0098112dc7c6ed8e4777", {
            cluster: "ap2",
        });

        const channel = pusher.subscribe("notifications-channel");
        channel.bind("new-notification", async function (data) {
            if (data) {
                this.notifications =
                    await notificationsServices.listUnreadNotification();
                this.un_read_notifications = Object.keys(this.notifications).length;

                console.log(this.notifications)
                helper.toggleToast(data.message, "success");
            }
        });
    },
    methods: {
        logout() {
            this.$store.dispatch('logout');
            this.$router.push({ name: 'osboha' })
        }
    },
};
</script>
<style>
.iq-top-navbar {
    padding: 1%;
    min-height: 4.563rem;
    line-height: unset;
}
</style>